.message {
    display: flex;
    flex-direction: column;
    align-items: end;
    color: #FFFFFF;
    margin-bottom: 30px;
}

.imageAndText {
    display: flex;
    flex-direction: row-reverse;
    gap: 10px;
    align-items: end;
}

.imageAndText img {
    width: 48px;
    height: 48px;
}

.text {
    height: 60px;
    background-color: #0066CC;
    padding: 8px 13px;
    border-radius: 10px;
}

.text:after {

}

.name {
    font-size: 18px;
    text-align: end;
    margin-bottom: 5px;
}

.messageText {
    font-size: 16px;
}

.time {
    color: black;
    font-size: 10px;
}